import React, { useEffect, useState } from 'react';
import { auth } from '../firebase';

const LeftColumn = ({ handleButtonClick, isActive, isLeftColumnVisible }) => {
  const [activeButton, setActiveButton] = useState(null);
  const [currentUser, setCurrentUser] = useState(null);
  const isMobile = window.innerWidth <= 768;

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged((user) => {
      if (user) {
        setCurrentUser(user); // Set the current user when authenticated
      } else {
        setCurrentUser(null); // Reset the current user if not authenticated
      }
    });

    return () => unsubscribe(); // Unsubscribe from the auth state listener
  }, []);

  const handleButtonClickInternal = (buttonName) => {
    // Call the parent function passed as a prop
    handleButtonClick(buttonName);
    setActiveButton(buttonName);
  };

  const goBack = () => {
    window.history.back(); // This will go back one page in the browser history
  };

    return (
<div className={`chat-left ${isLeftColumnVisible ? '' : 'hide'}`}>
        <div className="chatleft-column">
            <div className="d-none d-md-block mb-4"> 
            <div className="avatar-section">
            {currentUser && currentUser.photoURL && ( // Check if currentUser and photoURL exist
              <img
                src={currentUser.photoURL} // Use currentUser's photoURL
                alt="Avatar"
                className="w-100 h-auto rounded-circle"
                style={{ maxWidth: '12rem' }}
              />
            )}
  </div>

  
  
            <hr className="avatar-divider" />
            </div>
      
            <div className="LeftColumn-buttons ">
        <div className="chatbutton-container flex-wrap justify-content-center justify-content-md-start gap-2">
  
            <div className="button-container" onClick={() => handleButtonClickInternal('Chat History')}>
  <button type="button" className="py-3 px-3 gap-4 chat-btn">
                      <svg xmlns="http://www.w3.org/2000/svg" color= '#212529' fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25">
            <path strokeLinecap="round" strokeLinejoin="round" d="M7.5 8.25h9m-9 3H12m-9.75 1.51c0 1.6 1.123 2.994 2.707 3.227 1.129.166 2.27.293 3.423.379.35.026.67.21.865.501L12 21l2.755-4.133a1.14 1.14 0 01.865-.501 48.172 48.172 0 003.423-.379c1.584-.233 2.707-1.626 2.707-3.228V6.741c0-1.602-1.123-2.995-2.707-3.228A48.394 48.394 0 0012 3c-2.392 0-4.744.175-7.043.513C3.373 3.746 2.25 5.14 2.25 6.741v6.018z" />
      </svg>
      
          </button>
  
          {activeButton === 'Chat History' && <div className="activated-indicator"></div>}
          </div>
  
          <div className="button-container" onClick={() => handleButtonClickInternal('Contacts')}>
          <button type="button" className="py-3 px-3 gap-4 chat-btn">
            <svg xmlns="http://www.w3.org/2000/svg" color="#212529" fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25">
              <path strokeLinecap="round" strokeLinejoin="round" d="M19 7.5v3m0 0v3m0-3h3m-3 0h-3m-2.25-4.125a3.375 3.375 0 11-6.75 0 3.375 3.375 0 016.75 0zM4 19.235v-.11a6.375 6.375 0 0112.75 0v.109A12.318 12.318 0 0110.374 21c-2.331 0-4.512-.645-6.374-1.766z" />
            </svg>
          </button>
          {activeButton === 'Contacts' && <div className="activated-indicator"></div>}
        </div>
  
        <div className="button-container" onClick={() => handleButtonClickInternal('Profile')}>
      <button type="button" className="py-3 px-3 gap-4 chat-btn">
            <svg xmlns="http://www.w3.org/2000/svg" color= '#212529' fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25">
            <path strokeLinecap="round" strokeLinejoin="round" d="M15.75 6a3.75 3.75 0 11-7.5 0 3.75 3.75 0 017.5 0zM4.501 20.118a7.5 7.5 0 0114.998 0A17.933 17.933 0 0112 21.75c-2.676 0-5.216-.584-7.499-1.632z" />
      </svg>
          </button>
  
          {activeButton === 'Profile' && <div className="activated-indicator"></div>}
          </div>
  
       <div className="button-container" onClick={() => handleButtonClickInternal('Settings')}>
      <button type="button" className="py-3 px-3 gap-4 chat-btn">
            <svg xmlns="http://www.w3.org/2000/svg" color= '#212529' fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25">
            <path strokeLinecap="round" strokeLinejoin="round" d="M10.343 3.94c.09-.542.56-.94 1.11-.94h1.093c.55 0 1.02.398 1.11.94l.149.894c.07.424.384.764.78.93.398.164.855.142 1.205-.108l.737-.527a1.125 1.125 0 011.45.12l.773.774c.39.389.44 1.002.12 1.45l-.527.737c-.25.35-.272.806-.107 1.204.165.397.505.71.93.78l.893.15c.543.09.94.56.94 1.109v1.094c0 .55-.397 1.02-.94 1.11l-.893.149c-.425.07-.765.383-.93.78-.165.398-.143.854.107 1.204l.527.738c.32.447.269 1.06-.12 1.45l-.774.773a1.125 1.125 0 01-1.449.12l-.738-.527c-.35-.25-.806-.272-1.203-.107-.397.165-.71.505-.781.929l-.149.894c-.09.542-.56.94-1.11.94h-1.094c-.55 0-1.019-.398-1.11-.94l-.148-.894c-.071-.424-.384-.764-.781-.93-.398-.164-.854-.142-1.204.108l-.738.527c-.447.32-1.06.269-1.45-.12l-.773-.774a1.125 1.125 0 01-.12-1.45l.527-.737c.25-.35.273-.806.108-1.204-.165-.397-.505-.71-.93-.78l-.894-.15c-.542-.09-.94-.56-.94-1.109v-1.094c0-.55.398-1.02.94-1.11l.894-.149c.424-.07.765-.383.93-.78.165-.398.143-.854-.107-1.204l-.527-.738a1.125 1.125 0 01.12-1.45l.773-.773a1.125 1.125 0 011.45-.12l.737.527c.35.25.807.272 1.204.107.397-.165.71-.505.78-.929l.15-.894z" />
    <path strokeLinecap="round" strokeLinejoin="round" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
      </svg>
          </button>
          {activeButton === 'Settings' && <div className="activated-indicator"></div>}
          </div>
          {isMobile && (
            <div className="button-container" onClick={() => handleButtonClickInternal('Back')}>
              <button type="button" className="py-3 px-3 gap-4 chat-btn" onClick={goBack}>
                <svg xmlns="http://www.w3.org/2000/svg" color="#212529" fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25">
                  <path strokeLinecap="round" strokeLinejoin="round" d="M9 15L3 9m0 0l6-6M3 9h12a6 6 0 010 12h-3" />
                </svg>
              </button>
            </div>
          )}
  
   </div>
   
   </div>
   {!isMobile && (
   <div className='logout-container '>
<button type="button" className="py-3 px-3 gap-4 chat-btn" onClick={goBack}>
<svg xmlns="http://www.w3.org/2000/svg" color="#212529" fill="none" viewBox="0 0 24 24" strokeWidth={1} stroke="currentColor" height="25" width="25">
<path strokeLinecap="round" strokeLinejoin="round" d="M9 15L3 9m0 0l6-6M3 9h12a6 6 0 010 12h-3" />
</svg>
</button>
</div>
   )}
    
            </div>
            </div>
    );
  };
  export default LeftColumn;