import React, { useEffect, useState } from 'react';
import { auth, firestore } from '../firebase'; 
import { doc, getDoc } from 'firebase/firestore';

const Profile = () => {
  const [currentUser, setCurrentUser] = useState(null);
  const [description, setDescription] = useState('');
  const [city, setCity] = useState('');

  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged(async (user) => {
      if (user) {
        setCurrentUser(user);

        // Check if user exists in expertApplications collection
        const expertProfileRef = doc(firestore, 'expertApplications', user.uid);
        const expertProfileSnapshot = await getDoc(expertProfileRef);

        if (expertProfileSnapshot.exists()) {
          const expertProfileData = expertProfileSnapshot.data();
          setDescription(expertProfileData.description || ''); // Set description from expertApplications
          setCity(expertProfileData.city || ''); // Set city from expertApplications
        } else {
          // If user not found in expertApplications, check in userProfiles collection
          const userProfileRef = doc(firestore, 'userProfiles', user.uid);
          const userProfileSnapshot = await getDoc(userProfileRef);

          if (userProfileSnapshot.exists()) {
            const userProfileData = userProfileSnapshot.data();
            setDescription(userProfileData.description || ''); // Set description from userProfiles
            setCity(userProfileData.city || ''); // Set city from userProfiles
          } else {
            // Handle case where user doesn't exist in either collection
            setDescription('Description not found');
            setCity('City not found');
          }
        }
      } else {
        setCurrentUser(null);
        setDescription('');
        setCity('');
      }
    });

    return () => unsubscribe(); // Unsubscribe from the auth state listener
  }, []);


  return (
    <div className="d-flex flex-column gap-2 mt-3">
      <div className="justify-content-center align-items-center px-8">
        <div className="text-center">
          {currentUser && currentUser.photoURL && (
            <img
              className="medium-avatar mx-auto user-avatar mb-2"
              src={currentUser.photoURL}
              alt="User Avatar"
            />
          )}
          {currentUser && currentUser.displayName && (
            <>
              <h4>{currentUser.displayName}</h4>
              <p className='text-muted'>{description}</p> {/* Display bio here */}
              <hr className="avatar-divider" />
            </>
          )}
        </div>


</div>
<button
    className="d-flex chat-btn chat-btn-history align-items-center px-4 p-2  w-100  gap-4"
      style={{ transform: 'none' }}
      // Add any additional props you need
    >
                  <svg xmlns="http://www.w3.org/2000/svg" color="#212529" style={{ paddingRight: '5px' }} fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor" height="30" width="30">
                  <path strokeLinecap="round" strokeLinejoin="round" d="M21.75 6.75v10.5a2.25 2.25 0 01-2.25 2.25h-15a2.25 2.25 0 01-2.25-2.25V6.75m19.5 0A2.25 2.25 0 0019.5 4.5h-15a2.25 2.25 0 00-2.25 2.25m19.5 0v.243a2.25 2.25 0 01-1.07 1.916l-7.5 4.615a2.25 2.25 0 01-2.36 0L3.32 8.91a2.25 2.25 0 01-1.07-1.916V6.75" />
            </svg>

   My Pofile
    </button>
    <button
    className="d-flex chat-btn chat-btn-history align-items-center px-4 p-2  gap-4   w-100 "
      style={{ transform: 'none' }}
      // Add any additional props you need
    >
                  <svg xmlns="http://www.w3.org/2000/svg" color="#212529" style={{ paddingRight: '5px' }} fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor" height="30" width="30">
                  <path strokeLinecap="round" strokeLinejoin="round" d="M15 10.5a3 3 0 11-6 0 3 3 0 016 0z" />
  <path strokeLinecap="round" strokeLinejoin="round" d="M19.5 10.5c0 7.142-7.5 11.25-7.5 11.25S4.5 17.642 4.5 10.5a7.5 7.5 0 1115 0z" />
            </svg>

            {city}
    </button>
</div>

  
  );
};

export default Profile;
