import React, { useEffect, useState } from 'react';
import { collection, getDocs, query, orderBy, doc, getDoc } from 'firebase/firestore';
import { firestore, auth } from '../firebase';
import ReactMarkdown from 'react-markdown';
import LoadingCircle from '../components/LoadingCircle';

const ChatServiceconversation = ({ selectedSession, currentUserUID, otherUserUID, closeShareChat }) => {
  const [sharedChatData, setSharedChatData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [selectedMessageUID, setSelectedMessageUID] = useState(null);

  useEffect(() => {
    const determineSelectedMessageUID = async () => {
      try {
        const expertAppDocRef = doc(collection(firestore, 'expertApplications'), currentUserUID);
        const expertAppSnapshot = await getDoc(expertAppDocRef);
  
        // Set selectedMessageUID based on the presence of currentUserUID in the expertApplications collection
        setSelectedMessageUID(expertAppSnapshot.exists() ? otherUserUID : currentUserUID);
  
        console.log('Determining selectedMessageUID:', {
          otherUserUID,
          currentUserUID,
          existsInExpertApplications: expertAppSnapshot.exists(),
          determinedMessageUID: expertAppSnapshot.exists() ? otherUserUID : currentUserUID,
        });
      } catch (error) {
        console.error('Error determining selectedMessageUID:', error);
      }
    };
  
    determineSelectedMessageUID();
  }, [currentUserUID, otherUserUID]);

  
  useEffect(() => {
    const fetchSharedChatData = async () => {
      try {
        if (selectedSession && selectedMessageUID) { // Check if selectedMessageUID is defined
          const messagesCollectionRef = collection(firestore, 'users', selectedMessageUID, 'discussions', selectedSession, 'messages');
          console.log('Path for messagesCollectionRef:', messagesCollectionRef.path); // Log the path
          const messagesSnapshot = await getDocs(query(messagesCollectionRef, orderBy('createTime')));
          const messagesData = messagesSnapshot.docs.map((doc) => {
            const { prompt, response } = doc.data();
            return { prompt, response };
          });
          setSharedChatData(messagesData);
          setIsLoading(false); // Set loading to false when data is fetched
          console.log('Fetched messages for discussionId:', selectedSession, messagesData);
        }
      } catch (error) {
        console.error('Error fetching shared chat messages:', error);
        setIsLoading(false); // Set loading to false on error
      }
    };
  
    fetchSharedChatData();
  }, [selectedSession, selectedMessageUID]); // Include selectedMessageUID in the dependencies array
  

  if (!selectedMessageUID) {
    return null; // or render a loading state if needed until selectedMessageUID is determined
  }

  return (
   <div className="blurred-background">
    <div className="white-box-chathistory">
      <div className="go-back-svg go-back-svg-padding">
        <button onClick={closeShareChat}>
          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} color="black" stroke="currentColor" className="w-6 h-6">
            <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
      </div>

      <h2>Share link to Chat</h2>
      <div className='divider '></div>
      <p>
        Messages you send after creating your link won't be shared. Anyone with the URL will be able to view the shared chat.
      </p>
      <div className="shared-chat scroll-cat">
        {isLoading ? (
          <LoadingCircle />
        ) : (
          sharedChatData.map((message, index) => (
            <div key={index} className="chat-message">
              <strong>
                <ReactMarkdown>{message.prompt}</ReactMarkdown>

              </strong>
              <ReactMarkdown>{message.response}</ReactMarkdown>
              <div className='divider '></div>
            </div>
          ))
        )}
      </div>
    </div>
    </div>
  );
};

export default ChatServiceconversation;
