import React, { useState, useEffect, useRef } from 'react';
import { db } from '../firebase'; // Make sure to initialize Firebase correctly
import { 
  getDoc, collection, addDoc, query, onSnapshot, where, doc, updateDoc 
} from 'firebase/firestore';

const iceServers = [
  { urls: "stun:stun.l.google.com:19302"},
  { urls: "stun:stun.relay.metered.ca:80" },
  { urls: "turn:standard.relay.metered.ca:80", username: "9f24237eef08d63be9f50c85", credential: "bcSTypx8qHWJ7hH9" },
  { urls: "turn:standard.relay.metered.ca:80?transport=tcp", username: "9f24237eef08d63be9f50c85", credential: "bcSTypx8qHWJ7hH9" },
  { urls: "turn:standard.relay.metered.ca:443", username: "9f24237eef08d63be9f50c85", credential: "bcSTypx8qHWJ7hH9" },
  { urls: "turns:standard.relay.metered.ca:443?transport=tcp", username: "9f24237eef08d63be9f50c85", credential: "bcSTypx8qHWJ7hH9" },
];

const VideoChatApp = ({ currentUserUID, otherUserUID, onClose }) => { 
  const [peerConnection, setPeerConnection] = useState(null); 
  const [localStream, setLocalStream] = useState(null);
  const [remoteStream, setRemoteStream] = useState(null);
  const [callStatus, setCallStatus] = useState('idle'); 
  const [incomingCall, setIncomingCall] = useState(null); 
  const localVideoRef = useRef(); 
  const remoteVideoRef = useRef();

  // Declare 'pc' here for wider access
  let pc = null;
  let iceCandidates = []; // Initialize the array

  // Firestore listener for incoming calls
  useEffect(() => {
    const q = query(collection(db, 'calls'), where('callee', '==', currentUserUID), where('status', '==', 'requesting'));
    const unsubscribe = onSnapshot(q, (snapshot) => {
      snapshot.docChanges().forEach((change) => {
        if (change.type === 'added') {
          setIncomingCall({ ...change.doc.data(), id: change.doc.id }); 
        }
      });
    });
    return () => unsubscribe(); 
  }, [currentUserUID]); 

  // Attach video streams to refs
  useEffect(() => {
    if (localStream && localVideoRef.current && !localVideoRef.current.srcObject) {
      localVideoRef.current.srcObject = localStream;
    }
    if (remoteStream && remoteVideoRef.current && !remoteVideoRef.current.srcObject) {
      remoteVideoRef.current.srcObject = remoteStream;
    }
  }, [localStream, remoteStream]);

const startCall = async () => {
  console.log("startCall: Entered, existing pc:", pc); 
  setCallStatus('calling'); 

  try {
    // 1. Get local media
    const localStream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true });
    setLocalStream(localStream);

    // 2. Create NEW RTCPeerConnection (with handler cleanup)
    if (pc) { 
        pc.onicecandidate = null; // Clear previous handler
    }        
    pc = new RTCPeerConnection({ iceServers }); 
    console.log("startCall: New PC created"); 
    setPeerConnection(pc); 

    // 3. ICE Candidate Handling 
    pc.onicecandidate = (event) => { 
        if (event.candidate) {
          iceCandidates.push(event.candidate.toJSON()); 
          console.log("Caller ICE Candidate added.", iceCandidates);
        } else {
            console.log("Caller ICE Candidate gathering complete");
        }
    };

    // 4. Connection State Logging
    pc.oniceconnectionstatechange = (event) => {
      console.log("Caller Connection State: ", pc.connectionState);
    };

    // 5. Add Tracks to Peer Connection 
    localStream.getTracks().forEach((track) => pc.addTrack(track, localStream));

    // 6. Create Offer 
    const offer = await pc.createOffer();
    await pc.setLocalDescription(offer);

    // 7. Send Offer & ICE Candidates to Firestore 
    const callRef = await addDoc(collection(db, 'calls'), { caller: currentUserUID, callee: otherUserUID,  status: 'requesting', offer: offer.toJSON() });
    await updateDoc(callRef, { callerCandidates: iceCandidates }); 

    // 8. Listen for Answer from Firestore (Modified Listener)
    onSnapshot(doc(db, 'calls', callRef.id), async (docSnapshot) => {
      const callData = docSnapshot.data();
      if (callData.status === 'accepted') {
        console.log('Caller State before setRemoteDescription:', pc.connectionState);

        await pc.addIceCandidate(null); // Force ICE gathering

        // Wait for the connection state to become 'connecting'
        await new Promise(resolve => {
            if (pc.connectionState === 'connecting') {
                resolve();
            } else {
                const onStateChange = () => {
                   if (pc.connectionState === 'connecting') {
                       pc.onconnectionstatechange = null; // Remove listener
                       resolve();
                   }
                };
                pc.onconnectionstatechange = onStateChange;
            }
        });

        const answer = new RTCSessionDescription(callData.answer);
        await pc.setRemoteDescription(answer); 

        // Add ICE candidates from the answer:
        callData.calleeCandidates.forEach(candidate => {
          pc.addIceCandidate(new RTCIceCandidate(candidate));
        });
      }
    });

  } catch (error) {
    console.error("Error starting the call:", error);
    setCallStatus('idle');
    // Handle errors appropriately
  }
};


const handleAcceptCall = async () => {
  setCallStatus('connected');

  try {
    // 1. Get Local Media
    const localStream = await navigator.mediaDevices.getUserMedia({ video: true, audio: true });
    setLocalStream(localStream);

    // 2. Create RTCPeerConnection
    pc = new RTCPeerConnection({ iceServers });
    setPeerConnection(pc); 

    // Handle Remote Tracks 
    pc.ontrack = (event) => {
        if (event.streams && event.streams[0]) {
            setRemoteStream(event.streams[0]);
        }
    };

    // 3. Fetch Offer & ICE Candidates from Firestore
    const callDoc = await getDoc(doc(db, 'calls', incomingCall.id));
    const callData = callDoc.data();

    // Ensure the offer exists before proceeding
    if (!callData.offer) {
        console.error('Offer not found in Firestore');
        return; // Handle this situation
    }

    await pc.setRemoteDescription(new RTCSessionDescription(callData.offer));

    // 4. Create Answer
    const answer = await pc.createAnswer();
    await pc.setLocalDescription(answer);

    // 5. Send Answer & ICE Candidates to Firestore 
    let calleeCandidates = []; // Initialize the array

    pc.onicecandidate = (event) => {
      if (event.candidate) {
        calleeCandidates.push(event.candidate.toJSON());
        updateDoc(doc(db, 'calls', incomingCall.id), {
            status: 'accepted',
            answer: answer.toJSON(),
            calleeCandidates: calleeCandidates
        });
      }
    };

    // 6. Ensure ICE Candidate Gathering Completes
    await pc.addIceCandidate(null); // Wait for ICE gathering if necessary

    // 7. Connection State Logging (optional)
    pc.oniceconnectionstatechange = (event) => {
      console.log("Callee Connection State: ", pc.connectionState);
    };

  } catch (error) {
    console.error("Error handling incoming call:", error);
    // Handle errors appropriately 
  }
};



useEffect(() => {
  if (pc) {
    pc.onconnectionstatechange = (event) => {
      console.log("Caller Connection State: ", pc.connectionState);
    };
  }
}, [pc]);


  const handleRejectCall = async () => {
    await updateDoc(doc(db, 'calls', incomingCall.id), {
      status: 'rejected', 
    });
    setIncomingCall(null); 
  };

  const handleCallEnd = () => {
    // 1. Stop local and remote media tracks
    if (localStream) {
      localStream.getTracks().forEach((track) => track.stop());
    }
    if (remoteStream) {
      remoteStream.getTracks().forEach((track) => track.stop());
    }

    // 2. Close the peer connection
    if (peerConnection) {
      peerConnection.close();
    }

    // 3. Update Firestore (optional, but good practice)
    if (incomingCall) { // If the call was incoming 
      updateDoc(doc(db, 'calls', incomingCall.id), { status: 'ended' });
    }
    if (peerConnection) {
      peerConnection.onicecandidate = null; // Clear callee-side handler 
      peerConnection.close();
      console.log("Caller-side pc closed"); 
    }

    // 4. Reset UI state
    setCallStatus('idle');
    onClose(); 
  };

  

  return (
    <div className="video-chat-wrapper">
      <div className="go-back-svg">
        <button onClick={onClose}>
          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} color="black" stroke="currentColor" className="w-6 h-6">
            <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
          </svg>
        </button>
      </div>

      <h1>Video Chat App</h1>
      {callStatus === 'idle' && (
        <button onClick={startCall}>Start Video Call</button>
      )}

      {callStatus === 'calling' && <p>Calling...</p>}

      {callStatus === 'connected' && (
        <div className="call-active"> {/* Added a container for active call */}
          <video ref={localVideoRef} autoPlay muted style={{ width: '50%', height: 'auto' }} />
          <video ref={remoteVideoRef} autoPlay style={{ width: '50%', height: 'auto' }} />
 
        </div> 
      )}
         <button onClick={handleCallEnd}>End Call</button>
      {incomingCall && (
        <div className="incoming-call-modal">
          <p>Incoming call from: {incomingCall.caller}</p>
          <button onClick={handleAcceptCall}>Accept</button>
          <button onClick={handleRejectCall}>Reject</button>
        </div>
      )}
    </div>
  );
};

export default VideoChatApp; 