import React, { useState, useEffect } from 'react';
import LeftColumn from './LeftColumn';
import MiddleColumn from './MiddleColumn';
import RightColumn from './RightColumn';
import ChatHistory from './ChatHistory';
import Contacts from './Contacts';
import Profile from './Profile';
import { useLocation } from 'react-router-dom';

const Chat = () => {
  const [isActive, setIsActive] = useState(false);
  const [selectedMenu, setSelectedMenu] = useState('');
  const [isRightColumnVisible, setIsRightColumnVisible] = useState(true);
  const [isMiddleColumnVisible, setIsMiddleColumnVisible] = useState(true);
  const [isLeftColumnVisible, setIsLeftColumnVisible] = useState(true);
  const [activeButton, setActiveButton] = useState(null);

  const location = useLocation();
  const { state: locationState } = location;
  const { currentUserUID, otherUserUID: locationOtherUserUID } = locationState || {};

  const [otherUserUID, setOtherUserUID] = useState(locationOtherUserUID || null);

  const updateOtherUserID = (newOtherUserID, currentUserID) => {
    setOtherUserUID(newOtherUserID);
    console.log('Current User ID:', currentUserID); // Log the current user ID
  };

  useEffect(() => {
    if (locationState) {
      // Update otherUserUID if available in the location state
      setOtherUserUID(locationState.otherUserUID || null);
      console.log('Other User ID:', locationState.otherUserUID); // Log the other user ID
    }
  }, [locationState]);

  
  // useEffect to set 'Chat History' after component mounts
  useEffect(() => {
    setSelectedMenu('Chat History');
    // Check if the device is a mobile device and hide the right column initially
    const isMobile = window.matchMedia('(max-width: 768px)').matches;
    setIsRightColumnVisible(!isMobile);
  }, []);

  const handleButtonClick = (menu) => {
    setIsActive(!isActive);
    setSelectedMenu(menu);
    setIsLeftColumnVisible(true);
    setActiveButton(menu);  // Show the left column when a menu is selected
  };



  const toggleRightColumnVisibility = () => {
    setIsRightColumnVisible((prevVisibility) => !prevVisibility);
    setIsMiddleColumnVisible(false);
    setIsLeftColumnVisible(false); // Toggle the left column visibility
  };

  useEffect(() => {
    const isMobile = window.matchMedia('(max-width: 768px)').matches;
    if (isMobile && otherUserUID) {
      setIsLeftColumnVisible(false);
      setIsMiddleColumnVisible(false);
      setIsRightColumnVisible(true);
    }
  }, [otherUserUID]);
  
  const toggleColumns = () => {
    setIsRightColumnVisible((prevVisibility) => !prevVisibility);
    setIsMiddleColumnVisible(true); // Show the middle column
    setIsLeftColumnVisible(true); // Show the left column
  };

  useEffect(() => {
    const isMobile = window.matchMedia('(max-width: 768px)').matches;

    if (isMobile) {
      document.body.style.overflow = 'hidden';

      return () => {
        document.body.style.overflow = 'auto';
      };
    }
  }, []);



  return (
    <div className="chatcontainer">
      <LeftColumn handleButtonClick={handleButtonClick} isActive={isActive} 
              toggleRightColumnVisibility={toggleRightColumnVisibility}
              isLeftColumnVisible={isLeftColumnVisible} 
              activeButton={activeButton} // Pass activeButton state
              setActiveButton={setActiveButton} 
              otherUserUID={otherUserUID} 
              currentUserUID={currentUserUID} 
      />
      <MiddleColumn
        isActive={isActive}
        selectedMenu={selectedMenu}
        toggleRightColumnVisibility={toggleRightColumnVisibility}
        isMiddleColumnVisible={isMiddleColumnVisible} 
        activeButton={activeButton} // Pass activeButton state
        setActiveButton={setActiveButton} 
        currentUserUID={currentUserUID}
        updateOtherUserID={updateOtherUserID}
        otherUserUID={otherUserUID}
      >
        {selectedMenu === 'Chat History' && <ChatHistory isActive={isActive} selectedMenu={selectedMenu} />}
        {selectedMenu === 'Contacts' && <Contacts isActive={isActive} selectedMenu={selectedMenu} />}
        {selectedMenu === 'Profile' && <Profile isActive={isActive} selectedMenu={selectedMenu} />}
        {/* Add conditions for other menu options here */}
      </MiddleColumn>
      {otherUserUID && (
      <RightColumn
        isRightColumnVisible={isRightColumnVisible}
        toggleColumns={toggleColumns}
        otherUserUID={otherUserUID}
      />
    )}
  </div>

  );
};

export default Chat;