import React, { useState, useEffect, useRef } from 'react';
import ChatHeader from './Chatheader';
import { sendMessage, getMessages, markMessageAsRead } from './ChatService';
import { auth, onAuthStateChanged, db } from '../firebase';
import { onSnapshot, collection, getDocs } from 'firebase/firestore';


const RightColumn = ({ isRightColumnVisible, toggleColumns, otherUserUID }) => {
  const [messages, setMessages] = useState([]);
  const chatBoxRef = useRef(null);
  const messageInputRef = useRef(null);
  const [inputValue, setInputValue] = useState('');
  const [currentUserUID, setCurrentUserUID] = useState(null);
  const [isChattingWithSelf, setIsChattingWithSelf] = useState(false);
  const [textareaHeight, setTextareaHeight] = useState('28px');
  const maxTextareaHeight = 200;

  const [selectedFile, setSelectedFile] = useState(null);
  const fileInputRef = useRef(null);
  const [selectedFilePreview, setSelectedFilePreview] = useState(null);
  const [lastSeenMessageId, setLastSeenMessageId] = useState(null);

  const [initialLoad, setInitialLoad] = useState(true);


  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user) {
        setCurrentUserUID(user.uid);
      } else {
        setCurrentUserUID(null);
      }
    });
    return () => unsubscribe(); // Clean up the listener
  }, []);

  useEffect(() => {
    setIsChattingWithSelf(currentUserUID === otherUserUID);
  }, [currentUserUID, otherUserUID]);

  useEffect(() => {
    if (currentUserUID && otherUserUID) {
      getMessages(currentUserUID, otherUserUID, (newMessages) => {
        // Reverse the messages here and then set them in the state
        const reversedMessages = newMessages.slice(0).reverse();
        setMessages(reversedMessages);

        // Scroll to bottom when new messages are fetched
        scrollToBottom();
      });
    }
  }, [currentUserUID, otherUserUID]);

  useEffect(() => {
    markMessageAsRead(currentUserUID, otherUserUID);
  }, [currentUserUID, otherUserUID]);
  

  const isMobile = window.innerWidth <= 767; // Adjust the width threshold as needed

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      setSelectedFile(file);

      // Display a preview of the image
      if (file.type && file.type.startsWith('image/')) {
        const reader = new FileReader();
        reader.onload = (event) => {
          // Update the React state to show the image preview
          setSelectedFilePreview(event.target.result);
        };
        reader.readAsDataURL(file);
      }
    }
  };

  useEffect(() => {
    if (selectedFile && selectedFile.type && selectedFile.type.startsWith('image/')) {
      const reader = new FileReader();
      reader.onload = (event) => {
        setSelectedFilePreview(event.target.result);
      };
      reader.readAsDataURL(selectedFile);
    } else {
      setSelectedFilePreview(null);
    }
  }, [selectedFile]);
  

  const handleMessageSend = async () => {
    try {
      if (isChattingWithSelf) {
        console.error("Cannot send messages to yourself.");
        console.log('currentUserUID:', currentUserUID);
        console.log('otherUserUID:', otherUserUID);
        return;
      }
  
      if (inputValue.trim() === '' && !selectedFile) {
        return;
      }
  
      if (currentUserUID && otherUserUID) {
        const success = await sendMessage(currentUserUID, otherUserUID, inputValue, selectedFile);
        if (success) {
          setInputValue('');
          setSelectedFile(null);
          // Clear the image preview in the userui-input
          setSelectedFilePreview(null);
  
          if (!isMobile) {
            messageInputRef.current.focus();
          }
        }
      } else {
        console.error('currentUserUID or otherUserUID is undefined');
      }
    } catch (error) {
      console.error('Error sending message:', error);
    }
  };
  
  
  const scrollToBottom = () => {
    if (chatBoxRef.current) {
      chatBoxRef.current.scrollTop = chatBoxRef.current.scrollHeight;
    }
  };
  

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleTextareaResize = () => {
    const textarea = messageInputRef.current;
    if (textarea) {
      const newHeight = Math.min(textarea.scrollHeight, maxTextareaHeight);
      setTextareaHeight(`${newHeight}px`);
    }
  };
  
  
  
  useEffect(() => {
    handleTextareaResize();
  }, []);
  

  const handleInputChange = (e) => {
    setInputValue(e.target.value);
    handleTextareaResize();
  };

  useEffect(() => {
    handleTextareaResize();
  }, []);

  useEffect(() => {
    if (inputValue === '') {
      setTextareaHeight('28px');
    }
  }, [inputValue]);

  const formatTimestamp = (timestamp) => {
    if (timestamp) {
      const date = timestamp.toDate();
      const hours = date.getHours().toString().padStart(2, '0'); // Get hours in 24-hour format
      const minutes = date.getMinutes().toString().padStart(2, '0'); // Get minutes
      return `${hours}:${minutes}`; // Format to HH:MM
    }
    return ''; // Return an empty string if the timestamp is not available
  };

  const handleScroll = () => {
    // Calculate the last seen message based on scroll position
    if (chatBoxRef.current) {
      const { scrollTop, scrollHeight, clientHeight } = chatBoxRef.current;
      const lastVisibleMessageIndex = Math.floor(scrollTop / clientHeight);
      if (lastVisibleMessageIndex >= 0 && lastVisibleMessageIndex < messages.length) {
        const lastSeenMessage = messages[lastVisibleMessageIndex];
        setLastSeenMessageId(lastSeenMessage.id);
      }
    }
  };
  useEffect(() => {
    if (chatBoxRef.current) {
      chatBoxRef.current.addEventListener('scroll', handleScroll);
    }
    return () => {
      if (chatBoxRef.current) {
        chatBoxRef.current.removeEventListener('scroll', handleScroll);
      }
    };
  }, [chatBoxRef, messages]); // Add messages to dependency array



  

  useEffect(() => {
    if (currentUserUID && otherUserUID) {
      getMessages(currentUserUID, otherUserUID, (newMessages) => {
        const reversedMessages = newMessages.slice(0).reverse();
        setMessages(reversedMessages);
        scrollToBottom();
      });
    }
  }, [currentUserUID, otherUserUID]);

  

  useEffect(() => {
    markMessageAsRead(currentUserUID, otherUserUID, messages, lastSeenMessageId); // Pass lastSeenMessageId
  }, [messages, currentUserUID, otherUserUID, lastSeenMessageId]);

  useEffect(() => {
    if (isRightColumnVisible && initialLoad) {
      setInitialLoad(false);
    }
  }, [isRightColumnVisible, initialLoad]);


  return (
    <div className={`chat-right ${isRightColumnVisible && !initialLoad ? 'slide-in' : ''}`}>
      <ChatHeader toggleColumns={toggleColumns} otherUserUID={otherUserUID} currentUserUID={currentUserUID} />
      <div className="chat-column">
        <div className="chat-box" ref={chatBoxRef}>
        {Array.isArray(messages) && messages.length > 0 ? (
            messages.map((message, index) =>
              message.timestamp ? (
                <div
                  key={index}
                  className={message.sender === currentUserUID ? 'chat-bubble user-sent' : 'chat-bubble'}
                >
                  <div className="message">
                    {message.file && message.file.type && message.file.type.startsWith('image/') ? (
                      <img
                        src={message.file.url}
                        alt="Image"
                      className="rounded-image" 
                      />
                    ) : (
                      message.file && (
                        <a
                          href={message.file.url}
                          target="_blank"
                          rel="noopener noreferrer"
                        >
                          Download File
                        </a>
                      )
                    )}
                        {message.text && <p>{message.text}</p>}
                  </div>
                  <div
                    className={`timestamp small-timestamp ${
                      message.sender === currentUserUID ? 'user-sent-timestamp' : 'other-sent-timestamp'
                    }`}
                  >
                    {formatTimestamp(message.timestamp)}
                  </div>
                </div>
              ) : null
            )
          ) : (
            <div>No messages yet.</div>
)}

        </div>
        <div className="chatui-container">
    
            <input
              type="file"
              onChange={(e) => handleFileUpload(e)}
              style={{ display: 'none' }}
              ref={fileInputRef}
            />
            <button className="userui-left-button" onClick={() => fileInputRef.current.click()}>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                color="#212529"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={1}
                stroke="currentColor"
                height="25"
                width="25"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M18.375 12.739l-7.693 7.693a4.5 4.5 0 01-6.364-6.364l10.94-10.94A3 3 0 1119.5 7.372L8.552 18.32m.009-.01l-.01.01m5.699-9.941l-7.81 7.81a1.5 1.5 0 002.112 2.13"
                />
              </svg>
            </button>
            <button
              aria-label="Send Message"
              onClick={handleMessageSend}
              className=" userui-right-button "
            >
              <i className="fas fa-paper-plane"></i>
            </button>
            <div id="messageInputContainer">
            {selectedFile && (
              <div className="image-preview-container">
                <img
                  src={URL.createObjectURL(new Blob([selectedFile], { type: selectedFile.type }))}
                  alt="Image Preview"
                  style={{ maxWidth: '70%', height: 'auto' }}
                />
              </div>
            )}
            </div>
            <textarea
  type="text"
  placeholder="Type a message..."
  ref={messageInputRef}
  style={{ height: textareaHeight }}  // <-- Use the style attribute
  value={inputValue}
  onChange={handleInputChange}
  onKeyDown={(e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      handleMessageSend();
    }
  }}
  className="message-input scroll-col"
  autoComplete="off"
  inputMode="text"
/>

          </div>
          
          </div>
        </div>
  
  );
};

export default RightColumn;
