import React, { useState, useEffect } from 'react';
import { db, auth } from '../firebase'; // Import your Firebase instance
import { collection, doc, getDoc, setDoc, where, query, onSnapshot } from 'firebase/firestore';
import ChatServiceconversation from './ChatServiceconversation';
import VideoChatApp from './VideoChatApp'; 


const ChatHeader = ({ toggleColumns, otherUserUID, currentUserUID }) => {
  const [userData, setUserData] = useState({
    isMobile: false,
    photoURL: null,
    displayName: '',
    occupation: '',
    isShareChatOpen: false,
    selectedSession: null,
    selectedMessageUID: null,
  });

  const [showVideoCall, setShowVideoCall] = useState(false);
  const [incomingCall, setIncomingCall] = useState(null);

  const toggleVideoCall = () => {
    setShowVideoCall(prevState => !prevState);
  };

  const handleVideoChatClose = () => {
    setShowVideoCall(false);
  };

  useEffect(() => {
    const q = query(collection(db, 'calls'), where('callee', '==', currentUserUID), where('status', '==', 'requesting'));
    const unsubscribe = onSnapshot(q, (snapshot) => {
      snapshot.docChanges().forEach((change) => {
        if (change.type === 'added') {
          setIncomingCall({ ...change.doc.data(), id: change.doc.id }); 
        }
      });
    });
  
    return () => unsubscribe(); 
  }, [currentUserUID]); 

    useEffect(() => {
      const mediaQuery = window.matchMedia('(max-width: 768px)');
      setUserData((prevUserData) => ({ ...prevUserData, isMobile: mediaQuery.matches }));
  
      const handleResize = () => {
        setUserData((prevUserData) => ({ ...prevUserData, isMobile: mediaQuery.matches }));
      };
  
      mediaQuery.addListener(handleResize);
  
      return () => {
        mediaQuery.removeListener(handleResize);
      };
    }, []);
  
    const fetchUserData = async (userTypeRef) => {
      try {
        const userDocSnap = await getDoc(doc(userTypeRef, otherUserUID));
  
        if (userDocSnap.exists()) {
          const userData = userDocSnap.data();
          if (userData) {
            setUserData((prevUserData) => ({
              ...prevUserData,
              photoURL: userData.photoURL,
              displayName: userData.displayName ?? '',
              occupation: userData.occupation ?? '',
            }));
          }
        }
      } catch (error) {
        console.error('Error fetching user data:', error);
      }
    };
  
    useEffect(() => {
      const fetchData = async () => {
        try {
          if (otherUserUID) {
            const firestore = db;
            const expertAppsRef = collection(firestore, 'expertApplications');
            const expertDocSnap = await getDoc(doc(expertAppsRef, otherUserUID));
  
            if (expertDocSnap.exists()) {
              fetchUserData(expertAppsRef);
            } else {
              const userProfilesRef = collection(firestore, 'userProfiles');
              fetchUserData(userProfilesRef);
            }
          }
        } catch (error) {
          console.error('Error fetching user data:', error);
        }
      };
  
      fetchData();
    }, [otherUserUID]);
  
    useEffect(() => {
      const fetchSessionAndUpdate = async () => {
        try {
          if (otherUserUID && currentUserUID) {
            const firestore = db;
            const conversationsCollectionRef = collection(firestore, 'sharedConversations');
            let discussionID;
  
            const expertAppsRef = collection(firestore, 'expertApplications');
            const expertDocSnap = await getDoc(doc(expertAppsRef, currentUserUID));
  
            if (expertDocSnap.exists()) {
              discussionID = `${otherUserUID}_${currentUserUID}`;
            } else {
              discussionID = `${currentUserUID}_${otherUserUID}`;
            }
  
            const conversationRef = doc(conversationsCollectionRef, discussionID);
            const conversationSnapshot = await getDoc(conversationRef);
  
            if (conversationSnapshot.exists()) {
              const selectedSession = conversationSnapshot.data().selectedSession;
              setUserData((prevUserData) => ({
                ...prevUserData,
                selectedSession: selectedSession,
              }));
            } else {
            }
          } else {
            setUserData((prevUserData) => ({
              ...prevUserData,
              selectedMessageUID: null,
              selectedSession: null,
            }));
          }
        } catch (error) {
          console.error('Error fetching and updating session:', error);
        }
      };
  
      fetchSessionAndUpdate();
    }, [otherUserUID, currentUserUID]);
  
    const closeShareChat = () => {
      setUserData((prevUserData) => ({ ...prevUserData, isShareChatOpen: false }));
    };

    const renderCustomSVG = () => {
      if (userData.selectedSession) {
        return (
          <svg
          className='render-chat'
            xmlns="http://www.w3.org/2000/svg"
            color="#212529"
            fill={'none'}
            viewBox="0 0 24 24"
            strokeWidth={1}
            stroke="currentColor"
            height="25"
            width="25"
            onClick={() => {
              setUserData((prevUserData) => ({ ...prevUserData, isShareChatOpen: true }));
            }}
          >
        <path
        strokeLinecap="round"
        strokeLinejoin="round"
        d="M8.25 7.5V6.108c0-1.135.845-2.098 1.976-2.192.373-.03.748-.057 1.123-.08M15.75 18H18a2.25 2.25 0 002.25-2.25V6.108c0-1.135-.845-2.098-1.976-2.192a48.424 48.424 0 00-1.123-.08M15.75 18.75v-1.875a3.375 3.375 0 00-3.375-3.375h-1.5a1.125 1.125 0 01-1.125-1.125v-1.5A3.375 3.375 0 006.375 7.5H5.25m11.9-3.664A2.251 2.251 0 0015 2.25h-1.5a2.251 2.251 0 00-2.15 1.586m5.8 0c.065.21.1.433.1.664v.75h-6V4.5c0-.231.035-.454.1-.664M6.75 7.5H4.875c-.621 0-1.125.504-1.125 1.125v12c0 .621.504 1.125 1.125 1.125h9.75c.621 0 1.125-.504 1.125-1.125V16.5a9 9 0 00-9-9z"
        />
          </svg>
      );
    } else {
      return null;
    }
  };
  
    const handleImageClick = async () => {
      try {
        if (!otherUserUID) {
          throw new Error('otherUserUID is undefined or null');
        }
  
        const firestore = db;
        const expertAppsRef = collection(firestore, 'expertApplications');
        const expertDocSnap = await getDoc(doc(expertAppsRef, otherUserUID));
  
        if (expertDocSnap.exists()) {
          const customURL = expertDocSnap.data().customURL;
          window.location.href = `https://www.tioassist.io/expert/${customURL}`;
        } else {
          const userProfilesRef = collection(firestore, 'userProfiles');
          const userDocSnap = await getDoc(doc(userProfilesRef, otherUserUID));
  
          if (userDocSnap.exists()) {
            const customURL = userDocSnap.data().customURL;
            window.location.href = `https://www.tioassist.io/user/${customURL}`;
          } else {
          }
        }
      } catch (error) {
        console.error('Error handling image click:', error.message);
      }
    };
  
    return (
      <div className="chat-header" >
        <div className='chat-header-one'>
        {userData.isMobile && (
          <svg
            onClick={toggleColumns}
            xmlns="http://www.w3.org/2000/svg"
            color="#212529"
            fill="none"
            viewBox="0 0 24 24"
            strokeWidth={1}
            stroke="currentColor"
            height="25"
            width="25"
            style={{ marginRight: '8px' }}
          >
<path strokeLinecap="round" strokeLinejoin="round" d="M9 15L3 9m0 0l6-6M3 9h12a6 6 0 010 12h-3" />
          </svg>
        )}
  
        <div className="small-avatar-mobile" onClick={handleImageClick}>
          <img
            className="small-avatar-mobile "
            src={userData.photoURL}
            alt="Custom 404 Image"
          />
        </div>
        <div className="user-info">
          <div>
            <p className="font-weight-bold mb-0">{userData.displayName}</p>
            <p className="text-sm text-muted">{userData.occupation}</p>
          </div>
        </div>
        </div>
        <div className='chat-header-two'>
  
        <svg    
            xmlns="http://www.w3.org/2000/svg"
            color="#212529"
            fill={'none'}
            viewBox="0 0 24 24"
            strokeWidth={1}
            stroke="currentColor"
            height="25"
            id="call"
            width="25">
            
  <path strokeLinecap="round" strokeLinejoin="round" d="M2.25 6.75c0 8.284 6.716 15 15 15h2.25a2.25 2.25 0 0 0 2.25-2.25v-1.372c0-.516-.351-.966-.852-1.091l-4.423-1.106c-.44-.11-.902.055-1.173.417l-.97 1.293c-.282.376-.769.542-1.21.38a12.035 12.035 0 0 1-7.143-7.143c-.162-.441.004-.928.38-1.21l1.293-.97c.363-.271.527-.734.417-1.173L6.963 3.102a1.125 1.125 0 0 0-1.091-.852H4.5A2.25 2.25 0 0 0 2.25 4.5v2.25Z" />
</svg>

<svg    
            xmlns="http://www.w3.org/2000/svg"
            color="#212529"
            fill={'none'}
            viewBox="0 0 24 24"
            strokeWidth={1}
            stroke="currentColor"
            height="25"
            id="videocall"
            onClick={toggleVideoCall}
            width="25">
                <path strokeLinecap="round" strokeLinejoin="round" d="m15.75 10.5 4.72-4.72a.75.75 0 0 1 1.28.53v11.38a.75.75 0 0 1-1.28.53l-4.72-4.72M4.5 18.75h9a2.25 2.25 0 0 0 2.25-2.25v-9a2.25 2.25 0 0 0-2.25-2.25h-9A2.25 2.25 0 0 0 2.25 7.5v9a2.25 2.25 0 0 0 2.25 2.25Z" />
</svg>

{renderCustomSVG()}
  
        {userData.isShareChatOpen && (
          <ChatServiceconversation
            currentUserUID={currentUserUID}
            selectedSession={userData.selectedSession}
            otherUserUID={otherUserUID}
            closeShareChat={closeShareChat}
          />
        )}

        </div>
       {showVideoCall && (
        <VideoChatApp
          currentUserUID={currentUserUID}
          otherUserUID={otherUserUID}
          onClose={handleVideoChatClose} // Pass onClose function to VideoChatApp
        />
      )}
    </div>
    );
  };
  
  export default ChatHeader;