import React, { useEffect, useState } from 'react';
import ChatHistory from './ChatHistory';
import Contacts from './Contacts';
import Profile from './Profile';

const MiddleColumn = ({ selectedMenu, toggleRightColumnVisibility,  updateOtherUserID, isMiddleColumnVisible, setActiveButton, otherUserUID, currentUserUID}) => {
  const [currentMenu, setCurrentMenu] = useState(selectedMenu);
  const [isVisible, setIsVisible] = useState(true);
  const [isChatVisible, setIsChatVisible] = useState(true);

  useEffect(() => {
    setIsVisible(false);
    const timeoutId = setTimeout(() => {
      setCurrentMenu(selectedMenu);
      setIsVisible(true);
      setIsChatVisible(selectedMenu === 'Chat History'); // Set Chat visibility based on selectedMenu
    }, 300);

    return () => clearTimeout(timeoutId);
  }, [selectedMenu]);

  const switchToChatHistory = (updateActiveButton) => {
    setCurrentMenu('Chat History');
    setIsChatVisible(true);
    updateActiveButton('Chat History'); // Update the active button in the parent component
  };

  return (
    <div className={`chat-middle pt-4 ${isMiddleColumnVisible ? '' : 'hide'}`}>
      <div className={`chat-component ${isVisible && isChatVisible ? 'visible' : 'hidden'}`}>
        {currentMenu === 'Chat History' && (
          <ChatHistory otherUserUID={otherUserUID}  updateOtherUserID={updateOtherUserID}  toggleRightColumnVisibility={toggleRightColumnVisibility} currentUserUID={currentUserUID}/>
        )}
      </div>
      <div className={`contacts-component ${isVisible && !isChatVisible ? 'visible' : 'hidden'}`}>
      {currentMenu === 'Contacts' && (
          <Contacts switchToChatHistory={switchToChatHistory} setActiveButton={setActiveButton} toggleRightColumnVisibility={toggleRightColumnVisibility}  />
        )}
      </div>
      <div className={`profile-component ${isVisible && currentMenu === 'Profile' ? 'visible' : 'hidden'}`}>
        {currentMenu === 'Profile' && <Profile />}
      </div>
    </div>
  );
};

export default MiddleColumn;
