import { db, collection, doc, onSnapshot, setDoc, serverTimestamp, updateDoc, getDocs, query, where, addDoc } from '../firebase';
import { getStorage, ref, uploadBytes, getDownloadURL } from 'firebase/storage';

const storage = getStorage(); 

const chatsCollection = collection(db, 'chats');

const createChatID = (currentUserUID, otherUserUID) => {
  return currentUserUID < otherUserUID
    ? `${currentUserUID}_${otherUserUID}`
    : `${otherUserUID}_${currentUserUID}`;
};

const sendMessage = async (currentUserUID, otherUserUID, messageText, selectedFile) => {
  try {
    const chatID = createChatID(currentUserUID, otherUserUID);
    const chatDocRef = doc(chatsCollection, chatID);

    // First, create the parent chat document
    await setDoc(chatDocRef, { /* Chat document data */ });

    // Construct message data
    const messageObject = {
      text: messageText,
      sender: currentUserUID,
      receiver: otherUserUID,
      timestamp: serverTimestamp(),
      isRead: false,
    };

    // Then add the message to the 'messages' subcollection
    const messagesCollectionRef = collection(chatDocRef, 'messages');
    await addDoc(messagesCollectionRef, messageObject);

    return true;
  } catch (error) {
    console.error('Error sending message:', error);
    return false;
  }
};

const getMessages = (currentUserUID, otherUserUID, callback) => {
  try {
    const chatID = createChatID(currentUserUID, otherUserUID);
    const chatDocRef = doc(chatsCollection, chatID);
    const messagesCollectionRef = collection(chatDocRef, 'messages');

    onSnapshot(messagesCollectionRef, (snapshot) => {
      try {
        const messages = snapshot.docs.map((doc) => {
          return { id: doc.id, ...doc.data() };
        });
        const allMessages = messages.sort((a, b) => b.timestamp - a.timestamp);
        const reversedMessages = allMessages.reverse();
        callback(reversedMessages);
      } catch (error) {
        console.error('Error getting messages:', error);
        callback([]); // Handle errors by returning an empty array or taking appropriate action
      }
    });
  } catch (error) {
    console.error('Error getting messages:', error);
    callback([]); // Handle errors by returning an empty array or taking appropriate action
  }
};

const markMessageAsRead = async (currentUserUID, otherUserUID, messages) => {
  try {
    const chatID = createChatID(currentUserUID, otherUserUID);
    const chatDocRef = doc(chatsCollection, chatID);
    const messagesCollectionRef = collection(chatDocRef, 'messages');

    // Loop through each message
    messages.forEach(async (message) => {
      // Check if the message has already been marked as read
      if (!message.isRead && message.receiver === currentUserUID) {
        // If the current user is the receiver and the message is unread, mark it as read
        const messageDocRef = doc(messagesCollectionRef, message.id);
        await updateDoc(messageDocRef, { isRead: true });
        console.log('Message marked as read:', message.id);
      }
    });
  } catch (error) {
    console.error('Error marking messages as read:', error);
  }
};

export { sendMessage, getMessages, markMessageAsRead };
