import React, { useState, useEffect } from 'react';
import { collection, getDocs, getDoc, doc } from 'firebase/firestore';
import { Link } from 'react-router-dom'; // Import Link from React Router
import { firestore } from '../firebase';
import Fuse from 'fuse.js';


const Contacts = ({ toggleRightColumnVisibility }) => {
  const [showContacts, setShowContacts] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [searchResults, setSearchResults] = useState([]);

  useEffect(() => {
    if (searchQuery.trim() !== '') {
      const fetchData = async (collectionName) => {
        const collectionRef = collection(firestore, collectionName);
        const querySnapshot = await getDocs(collectionRef);
        const data = [];

        querySnapshot.forEach((doc) => {
          const item = { ...doc.data(), otherUserUID: doc.id };
          data.push(item);
        });

        return data;
      };

      const fetchExpertData = fetchData('expertApplications');
      const fetchProfileData = fetchData('userProfiles');

      Promise.all([fetchExpertData, fetchProfileData])
        .then(([expertsData, profilesData]) => {
          const combinedData = [...expertsData, ...profilesData];

          const fuseOptions = {
            keys: ['displayName', 'occupation', 'keywords'],
            includeScore: true,
            includeMatches: true,
            threshold: 0.4,
          };

          const fuse = new Fuse(combinedData, fuseOptions);
          const searchResults = fuse.search(searchQuery).map((result) => result.item);

          setSearchResults(searchResults);
        })
        .catch((error) => {
          console.error('Error fetching search results:', error);
        });
    } else {
      setSearchResults([]);
    }
  }, [searchQuery]);

  const handleSearchInputChange = (e) => {
    setSearchQuery(e.target.value);
  };

  const handleSearchKeyPress = (e) => {
    if (e.key === 'Enter') {
      toggleRightColumnVisibility();
    }
  };

  const handleButtonClick = async (result) => {
    toggleRightColumnVisibility();

    try {
      const expertAppsRef = collection(firestore, 'expertApplications');
      const expertDocSnap = await getDoc(doc(expertAppsRef, result.otherUserUID));

      console.log('Expert Doc Snap:', expertDocSnap.data());

      if (expertDocSnap.exists()) {
        const customURL = expertDocSnap.data().customURL;
        console.log('Custom URL:', customURL);
        window.location.href = `/expert/${customURL}`; // Use React Router's Link path
      } else {
        const userProfilesRef = collection(firestore, 'userProfiles');
        const userDocSnap = await getDoc(doc(userProfilesRef, result.otherUserUID));

        console.log('User Doc Snap:', userDocSnap.data());

        if (userDocSnap.exists()) {
          const customURL = userDocSnap.data().customURL;
          console.log('Custom URL:', customURL);
          window.location.href = `/user/${customURL}`; // Use React Router's Link path
        } else {
          console.log('User profile not found for the specified UID.');
        }
      }
    } catch (error) {
      console.error('Error handling button click:', error.message);
    }
  };
  
  return (
    <div className="d-flex flex-column gap-2 mt-3">
      <div className="input-container">
        <input
          type="text"
          className="chat-search-input"
          placeholder="Search for Experts"
          value={searchQuery}
          onChange={handleSearchInputChange}
          onKeyDown={handleSearchKeyPress}
        />
      </div>
      {searchResults.map((result, index) => (
          <Link>
        <button
          key={index}
          className="py-2 px-2 pe-5 gap-4 chat-btn"
          onClick={() => handleButtonClick(result)}
        >
          <div className="row align-items-center">
            <div className="col-auto">
              <div className="small-avatar">
                <img
                  src={result.photoURL || 'avatar.png'}
                  alt="Avatar"
                  className="w-100 h-auto rounded-circle"
                  style={{ maxWidth: '12rem' }}
                />
              </div>
            </div>
            <div className="col">
              <div className="text-end">
                <p className="font-weight-bold mb-0">{result.displayName}</p>
                <p className="text-sm text-muted">{result.occupation}</p>
              </div>
            </div>
          </div>
        </button>
         </Link>
      ))}
    </div>
  );
};

export default Contacts;
